import React from 'react'
import styled from "styled-components"
import { buildStyles,CircularProgressbarWithChildren } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';


const ProgressCircle = ({title,percentage,img}) => {
    return (
        <ProgressCircleStyled>
              <h6>{title}</h6> 
              <CircularProgressbarWithChildren value={percentage} strokeWidth={7} styles={buildStyles({textColor: '#82AB92',pathColor: '#82AB92', trailColor: '#fcfafa', strokeLinecap: 'round'})}> 
                 <img style={{ width: 40, marginTop: -5}} src={img} alt="Freepik,Pixel Perfect" />
                 <div >
                    <strong>{percentage}%</strong>
                </div>
              </CircularProgressbarWithChildren>
        </ProgressCircleStyled>
  
    )
}

const ProgressCircleStyled = styled.section`
    margin: 1rem auto;
    text-align:center;
    width:150px;
    height:150px;
 strong{
    color: var(--icon-green-color);
    font-size: 16px; 
    }  
 h6{
    padding: .2rem ;
    color: var(--icon-green-color);
   }
`

export default ProgressCircle;


